/*////Container/////*/
/*///Colors////*/
/*////Font size///*/
@font-face {
  font-family: 'OpenSans';
  src: url('../fonts/OpenSans/OpenSans-Regular.ttf') format('truetype');
  font-weight: 400;
}
@font-face {
  font-family: 'OpenSans';
  src: url('../fonts/OpenSans/OpenSans-SemiBold.ttf') format('truetype');
  font-weight: 600;
}
@font-face {
  font-family: 'OpenSans';
  src: url('../fonts/OpenSans/OpenSans-Bold.ttf') format('truetype');
  font-weight: 700;
}
@font-face {
  font-family: 'Montserrat';
  src: url('../fonts/Montserrat/Montserrat-Regular.ttf') format('truetype');
  font-weight: 400;
}
@font-face {
  font-family: 'Montserrat';
  src: url('../fonts/Montserrat/Montserrat-SemiBold.ttf') format('truetype');
  font-weight: 600;
}
@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
* {
  box-sizing: border-box;
}
/*Body container*/
.background {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #ECF1F8;
  opacity: 0.4;
  z-index: -1;
}
.container {
  max-width: 1200px;
  width: 100%;
}
/*Header*/
.header {
  width: 100%;
  height: 112px;
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  font-family: 'OpenSans', sans-serif;
  font-size: 16px;
}
.header__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 19px 10px 23px 16px;
}
.header__content {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_shadow {
  box-shadow: 0 12px 12px rgba(0, 0, 0, 0.06);
}
.profile {
  display: flex;
  align-items: center;
}
.profile__user {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 17px;
}
.profile__name {
  font-weight: 400;
  margin: 0 21px 0 0;
  color: #3E3F58;
  letter-spacing: -0.5px;
}
.profile__picture {
  margin-right: 12px;
}
.profile__link {
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile__link img {
  max-width: 100%;
}
.icons {
  display: flex;
}
.icons__link {
  text-decoration: none;
}
.menu {
  display: flex;
  font-size: 0.87em;
  text-transform: uppercase;
  font-weight: 600;
}
.menu__link {
  margin-right: 37px;
  color: #3E4056;
  text-decoration: none;
  opacity: 0.6;
}
.menu__link_active {
  position: relative;
  opacity: 1;
}
.menu__link_active:after {
  content: "";
  position: absolute;
  top: 35px;
  left: 0;
  width: 34px;
  height: 4px;
  background: linear-gradient(135deg, #FF974F 0%, #E32C75 100%);
}
/*Main*/
.main {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 72px 15px 0;
}
.action-button {
  position: absolute;
  top: 7px;
  right: 5px;
  width: 64px;
  height: 64px;
  background: linear-gradient(135deg, #FF974F 0%, #E32C75 100%);
  border-radius: 50%;
}
.action-button:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 2px;
  background-color: #ffffff;
}
.action-button:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 2px;
  height: 20px;
  background-color: #ffffff;
}
/*User*/
.user {
  position: relative;
  max-width: 350px;
  display: flex;
  flex-direction: column;
  font-family: 'OpenSans', sans-serif;
  margin-right: 36px;
  font-weight: 400;
  font-size: 16px;
  color: #3E3F58;
}
.user__details {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 32px;
}
.user__avatar {
  margin-right: 25px;
}
.user__info {
  display: flex;
  flex-direction: column;
}
.user__name {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 1.5em;
  margin: 0 0 8px 0;
}
.user__nickname {
  margin: 0 0 16px 0;
  letter-spacing: -0.5px;
}
.user__edit {
  display: flex;
}
.user__edit-icon {
  margin-right: 7px;
}
.user__edit-text {
  color: #F05D63;
}
.user__connections {
  padding-left: 4px;
  margin-bottom: 26px;
}
.user__description {
  margin: 0;
  line-height: 1.5;
}
.user__popup {
  right: 15px;
}
.connections {
  display: flex;
}
.connections__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 48px;
}
.connections__content:last-child {
  margin-right: 0;
}
.connections__number {
  font-size: 1.13em;
  margin-bottom: 5px;
}
.connections__type {
  margin: 0;
}
.popup {
  position: absolute;
}
.popup__dots {
  cursor: pointer;
}
.popup__content {
  position: absolute;
  top: 42px;
  left: 2px;
  display: flex;
  flex-direction: column;
  padding: 24px 0;
  background-color: #ffffff;
  border-radius: 8px;
  font-weight: 600;
  visibility: hidden;
}
.popup__content.show {
  visibility: visible;
  animation: appear 0.5s;
}
.popup__row {
  display: flex;
  padding: 8px 23px;
}
.popup__row:hover {
  background-color: #ECF1F8;
  backdrop-filter: blur(54.3656px);
  cursor: pointer;
}
.popup__icon {
  margin-right: 15px;
}
.popup__text {
  white-space: nowrap;
  margin: 0;
}
/*Cards*/
.cards {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  font-family: 'OpenSans', sans-serif;
  font-size: 16px;
}
.cards__area {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  font-size: 16px;
}
.card__wrapper {
  padding: 24px;
  margin: 12px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 24px 24px rgba(0, 0, 0, 0.12);
}
.card__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card__header-text {
  margin: 0;
}
.card__tag {
  display: block;
  max-width: 247px;
  padding: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: #ECF1F8;
  border-radius: 6px;
  font-weight: 600;
}
.card__footer {
  display: flex;
}
.card__stats {
  display: flex;
}
.card__views,
.card__likes {
  display: flex;
  align-items: center;
}
.switcher {
  display: flex;
  align-items: center;
  padding-left: 11px;
  font-size: 16px;
}
.switcher__text {
  margin: 0;
  font-weight: 600;
}
.switcher__text:not(:last-child) {
  margin-right: 30px;
}
.switcher__label {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
  margin-right: 30px;
}
.switcher__input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switcher__input:checked + .switcher__slider {
  background-color: #F05D63;
}
.switcher__input:focus + .switcher__slider {
  box-shadow: 0 0 1px #F05D63;
}
.switcher__input:checked + .switcher__slider:before {
  transform: translateX(16px);
}
.switcher__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #B2B3BB;
  transition: 0.3s;
}
.switcher__slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: 0.3s;
}
.switcher__round {
  border-radius: 36px;
}
.switcher__round:before {
  border-radius: 50%;
}
